<template>
    <button
        v-if="data"
        @click="this.$router.push({ name: 'Graph', params: { type: 'nazionale', target: '' } })"
        class="rounded block m-auto mb-8 bg-blue-600 p-4 text-3xl text-white focus:outline-none focus:border-transparent transform active:scale-95"
    >
        Vai al Grafico
    </button>
    <div class="text-center">
        <DataGrid :data="data" :time="time" />
    </div>
</template>

<script>
import DataGrid from "@/components/DataGrid";

export default {
    name: "All",
    components: {
        DataGrid,
    },
    data() {
        return {
            data: {},
            time: new Date(),
        };
    },
    async created() {
        const req = await fetch("api/nazionale/latest");
        const json = await req.json();

        let { data, ...newJson } = json;
        this.data = newJson;
        this.time = new Date(data);
    },
};
</script>
