<template>
    <h1 class="m-8 text-2xl font-bold">
        Dati del
        {{ time.toLocaleString("default", { day: "numeric", month: "long", year: "numeric" }) }}
    </h1>
    <div class="grid xl:grid-cols-4 lg:grid-cols-3 sm:grid-cols-2 gap-4">
        <DataCard
            v-for="(d, i) in data"
            :key="i"
            :title="i.replaceAll('_', ' ').replace(/(^\w|\s\w)/g, m => m.toUpperCase())"
            :data="d ?? 'Non Disponibile'"
        />
    </div>
</template>

<script>
import DataCard from "@/components/DataCard";

export default {
    name: "DataGrid",
    props: {
        data: Object,
        time: Date,
    },
    components: {
        DataCard,
    },
};
</script>
